
import http from './http'


export default {
  //登录
  logining(params) {
    return http.post('tp5/public/apimanage/login/login', params)
  },
  // 退出登录
  logouting(params) {
    return http.post('tp5/public/apimanage/login/logout', params)
  },
  //注册
  register(params) {
    return http.post('tp5/public/apimanage/Registered/add_request', params)
  },
  //资源目录列表   
  cataList(params) {
    return http.post('tp5/public/apimanage/cata_Information/cata_index', params)
  },
   //列表   
   TotalList(params) {
    return http.post('tp5/public/apimanage/api_information/show_api_detail', params)
  },
  //绑定目录api
  Bindcata(params) {
    return http.post('tp5/public/apimanage/api_information/apilist', params)
  },
   //绑定目录api  弹窗列表  确定
  BindCataApi(params) {
    return http.post('tp5/public/apimanage/cata_information/bind_api', params)
  },
  //列表 弹窗 确定
  BindListApi(params) {
    return http.post('tp5/public/apimanage/api_information/bind_catalog',params)
  },
  //列表 弹窗 修改
  UpdateApi(params) {
    return http.post('tp5/public/apimanage/api_information/puyuanUpdate',params)
  },
   //列表 弹窗 删除
   DelApi(params) {
    return http.post('tp5/public/apimanage/api_information/puyuanDelete',params)
  },
   //列表 弹窗 查看
  LookApi(params) {
    return http.post('tp5/public/apimanage/api_information/one_detail',params)
  },
    //管理 总计
    Allnum(params) {
    return http.post('tp5/public/apimanage/admin/get_api',params)
  },
}



