<template>
	<div class="home">
	    <div class="headers">
			<componentHeader></componentHeader>
		</div>
		<div class="title">
			<div class="box"
			 v-for="(item,index) in list"
			  :class="Typenum == index ? 'active' : '' "
			  @click="switchType(index)"
			 :key="index">{{item.name}}</div>
		</div>
	
		    <component v-bind:is="currentTabComponent"></component>


	</div>
</template>
<style lang="less" scoped>
@import "./home.less";

</style>

<script>
import { mapState,mapMutations } from 'vuex'
import api from "../../api/qingpu-api";

import componentHeader from '../../components/headers/headers'

import componentRegister from '../register/register'
import componentResources from '../Resources/Resources'
import componentList from '../List/List'

export default {
	name: "home",
	data() {
		return{
			list:[{
				name:"注册",id:1,
			},
			{
				name:"资源目录列表",id:2,
			},{
				name:"API列表",id:3,
			}],
			Typenum:0,
			currentTabComponent:'componentRegister'
		}
	},
	components:{
        componentHeader,componentRegister,componentResources,componentList
	},
	mounted(){
		this.init()
		
	},
	computed:{
		...mapState(['pageType'])
	},
	methods:{
		switchType(ev){
			this.Typenum = ev
			if(ev == 0){
                this.currentTabComponent = "componentRegister"
				if( localStorage.getItem('accessToken') ){
					localStorage.removeItem('accessToken')
					localStorage.setItem('accessToken',"componentRegister")
				}
				
			}
			else if(ev == 1){
                this.currentTabComponent = "componentResources"
				if( localStorage.getItem('accessToken') ){
					localStorage.removeItem('accessToken')
					localStorage.setItem('accessToken',"componentResources")
				}
			}
			else{
                this.currentTabComponent = "componentList"
				if( localStorage.getItem('accessToken') ){
					localStorage.removeItem('accessToken')
					localStorage.setItem('accessToken', "componentList")
				}
			
			}
			
		},
		init(){
			var self = this
			if (window.performance.navigation.type == 1) {
		     	this.currentTabComponent = localStorage.getItem('accessToken')
				if(localStorage.getItem('accessToken') == "componentRegister"){
					self.Typenum = 0
				}
				else if(localStorage.getItem('accessToken') == "componentResources"){
					self.Typenum =1
				}
				else if(localStorage.getItem('accessToken') == "componentList"){
					self.Typenum = 2
				}
			}else{
				
			}
			//console.log(localStorage.getItem('accessToken'))

		}
	}
}
</script>





