<template>
	<div class="List">
       <el-table :data="tableDataList.slice((currentPage - 1) * pageSize, currentPage * pageSize)">
            <el-table-column prop="index" label="序号">
	        </el-table-column>
			<el-table-column prop="name" label="API名称">
            </el-table-column>
	    	<el-table-column prop="address" label="API地址">
	        </el-table-column>
	        <el-table-column prop="miaoshu" label="API描述">
	        </el-table-column>
			 <el-table-column prop="miaoshu" label="API描述">
	        </el-table-column>
	        <el-table-column prop="status" label="目录绑定状态">
	        </el-table-column>
			<el-table-column prop="CHname" label="已绑定目录名称">
	        </el-table-column>
	        
			<el-table-column label="资源操作">
	        	<template slot-scope="scope">
						<span
						    class="fontSize"
							v-if="scope.row.status == '未绑定'"
							@click="handleEdit( scope.row)">绑定目录
						</span>
						<span
						   	v-if="scope.row.status == '已绑定'"
						    class="fontSizeHover">已绑定
						</span>
					</template>
	        </el-table-column>
			<el-table-column label="操作">
							<template slot-scope="scope">
									<span
										class="fontSize"
										@click="Looktype(scope.row)">查看
									</span>
									<span
										class="fontSize"
										@click="Updateype(scope.row)">编辑
									</span>
									<span
										class="fontSize"
										@click="Deltype(scope.row)">删除
									</span>
								</template>
						</el-table-column>
	    </el-table>
		<el-pagination 
			background
			layout="prev, pager, next"
			:current-page="currentPage"
			:page-size="pageSize"
			@current-change="current_change"
			:total="tableDataList.length">
		</el-pagination>
	    <el-dialog
			:title="dialogtitle"
			:visible.sync="dialogVisible"
			width="80%"
			:before-close="handleClose">
				<div class="diatable">
			        <el-table :data="cataList.slice((currentPage2 - 1) * pageSize2, currentPage2 * pageSize2)">
						<el-table-column prop="index" label="序号">
						</el-table-column>
						<el-table-column prop="bsname" label="目录标识号">
						</el-table-column>
						<el-table-column prop="id" label="目录ID">
						</el-table-column>
						<el-table-column prop="name" label="目录名称">
						</el-table-column>
						<el-table-column prop="type" label="所属单位">
						</el-table-column>
						<el-table-column prop="status" label="目录状态">
						</el-table-column>
						<el-table-column label="资源操作">
							<template slot-scope="scope">
									<span
										class="fontSize"
										@click="Edittype(scope.row)">点击确定
									</span>
								
								</template>
						</el-table-column>
					
					</el-table>
					<el-pagination 
						background
						layout="prev, pager, next"
						:current-page="currentPage2"
						:page-size="pageSize2"
						@current-change ="current_change2"
						:total="cataList.length">
					</el-pagination>
			    </div>
		</el-dialog>
		<el-dialog 
		    custom-class="diaform"
			:title="dialogtitleLook"
			:visible.sync="dialogVisibleLook"
			width="80%"
			:before-close="handleCloseLook">
			<div class="look" v-if="showLook">
			    <el-form ref="form" :model="form" label-width="36%">
					<div class="left">
						<el-form-item label="入参信息：">
							<el-input v-model="form.rcxinxi" disabled></el-input>
						</el-form-item>
						<el-form-item label="API地址：">
							<el-input v-model="form.apiaddress" disabled></el-input>
						</el-form-item>
						<el-form-item label="API描述：">
							<el-input v-model="form.apimiaoshu" disabled></el-input>
						</el-form-item>
						<el-form-item label="请求方式：">
							<el-input v-model="form.methods" disabled></el-input>
						</el-form-item>
						<el-form-item label="绑定目录id：">
							<el-input v-model="form.id" disabled></el-input>
						</el-form-item>
						<el-form-item label="申请服务是否携带附件：" >
							<el-select v-model="form.condition" placeholder="否" disabled>
								<el-option label="是" value="1"></el-option>
								<el-option label="否" value="0"></el-option>
							</el-select>
						</el-form-item>
						<el-form-item label="服务注册者姓名：" >
							<el-input v-model="form.name" disabled> </el-input>
						</el-form-item>
						<!-- <el-form-item label="注册时间：" >
							<el-input v-model="form.time" disabled></el-input>
						</el-form-item>
						<el-form-item label="服务有效日期：" >
							<el-input v-model="form.effective" disabled></el-input>
						</el-form-item> -->
						<el-form-item label="注册时间：">
							<el-date-picker
									v-model="form.time"
									type="datetime"
									format="yyyy-MM-dd hh:mm"
									value-format="yyyy-MM-dd hh:mm"
									disabled
									placeholder="选择日期时间">
							</el-date-picker>
						</el-form-item>
						<el-form-item label="服务有效日期：">
								<el-date-picker
									v-model="form.effective"
									type="datetime"
									format="yyyy-MM-dd hh:mm"
									value-format="yyyy-MM-dd hh:mm"
									disabled
									placeholder="选择日期时间">
							</el-date-picker>
						</el-form-item> 

						<el-form-item label="服务描述：" >
							<el-input type="textarea" :rows="3" v-model="form.desc" disabled></el-input>
						</el-form-item>

					</div>
					<div class="right">
						<el-form-item label="实例信息：" >
							<el-input v-model="form.Example" disabled></el-input>
						</el-form-item>
						<el-form-item label="API名称：">
							<el-input v-model="form.ApiName" disabled></el-input>
						</el-form-item>
						<el-form-item label="协议：">
							<el-input v-model="form.Agreement" disabled></el-input>
						</el-form-item>
						<el-form-item label="返回类型：">
							<el-input v-model="form.rType" disabled></el-input>
						</el-form-item>
						<el-form-item label="服务中文名称：">
							<el-input v-model="form.ServiceName" disabled></el-input>
						</el-form-item>
						<el-form-item label="服务版本：">
							<el-input v-model="form.ServiceType" disabled></el-input>
						</el-form-item>
						<el-form-item label="服务注册机构名称：">
							<el-input v-model="form.ServiceHouse" disabled></el-input>
						</el-form-item>
						<el-form-item label="发布时间：">
								<el-date-picker
									v-model="form.PublishingTime"
									type="datetime"
									disabled
									format="yyyy-MM-dd hh:mm"
									value-format="yyyy-MM-dd hh:mm"
									placeholder="选择日期时间">
							</el-date-picker>
						</el-form-item>

						<el-form-item label="公开方式：">
							<el-select v-model="form.Open"  disabled>
								<el-option label="有条件共享" value="1"></el-option>
								<el-option label="无条件共享" value="0"></el-option>
							</el-select>
						</el-form-item>
						<el-form-item label="密钥认证：">
							<el-input type="textarea" :rows="3" v-model="form.SecretKey" disabled></el-input>
						</el-form-item>
					</div>
				</el-form>
			</div>
			<div class="look2" v-if= "showUpt">
			    <el-form ref="form" :model="form" label-width="36%">
					<div class="left">
						<el-form-item label="入参信息：">
							<el-input v-model="form.rcxinxi"></el-input>
						</el-form-item>
						<el-form-item label="API地址：">
							<el-input v-model="form.apiaddress" ></el-input>
						</el-form-item>
						<el-form-item label="API描述：">
							<el-input v-model="form.apimiaoshu"></el-input>
						</el-form-item>
						<el-form-item label="请求方式：">
							<el-input v-model="form.methods"></el-input>
						</el-form-item>
					
						<el-form-item label="申请服务是否携带附件：" >
							<el-select v-model="form.condition">
								<el-option label="是" value="1"></el-option>
								<el-option label="否" value="0"></el-option>
							</el-select>
						</el-form-item>
						<el-form-item label="服务注册者姓名：" >
							<el-input v-model="form.name"> </el-input>
						</el-form-item>
						<!-- <el-form-item label="注册时间：" >
							<el-input v-model="form.time"></el-input>
						</el-form-item>
						<el-form-item label="服务有效日期：" >
							<el-input v-model="form.effective"></el-input>
						</el-form-item> -->
					  <el-form-item label="注册时间：">
							<el-date-picker
									v-model="form.time"
									type="datetime"
									format="yyyy-MM-dd hh:mm"
									value-format="yyyy-MM-dd hh:mm"
									placeholder="选择注册时间">
							</el-date-picker>
						</el-form-item>
						<el-form-item label="截止有效日期：">
								<el-date-picker
									v-model="form.effective"
									type="datetime"
									format="yyyy-MM-dd hh:mm"
									value-format="yyyy-MM-dd hh:mm"
									:picker-options="pickerOptionsEnd"
									placeholder="选择截止有效日期">
							</el-date-picker>
						</el-form-item> 

						<el-form-item label="服务描述：" >
							<el-input type="textarea" :rows="3" v-model="form.desc"></el-input>
						</el-form-item>

					</div>
					<div class="right">
						<el-form-item label="实例信息：">
							<el-input v-model="form.Example" ></el-input>
						</el-form-item>
						<el-form-item label="API名称：">
							<el-input v-model="form.ApiName"></el-input>
						</el-form-item>
						<el-form-item label="协议：">
							<el-input v-model="form.Agreement"></el-input>
						</el-form-item>
						<el-form-item label="返回类型：">
							<el-input v-model="form.rType"></el-input>
						</el-form-item>
						<el-form-item label="服务中文名称：">
							<el-input v-model="form.ServiceName"></el-input>
						</el-form-item>
						<el-form-item label="服务版本：">
							<el-input v-model="form.ServiceType"></el-input>
						</el-form-item>
						<el-form-item label="服务注册机构名称：">
							<el-input v-model="form.ServiceHouse" disabled></el-input>
						</el-form-item>
						<el-form-item label="发布时间：">
								<el-date-picker
									v-model="form.PublishingTime"
									type="datetime"
									format="yyyy-MM-dd hh:mm"
									value-format="yyyy-MM-dd hh:mm"
									placeholder="选择发布时间">
							</el-date-picker>
						</el-form-item>

						<el-form-item label="公开方式：">
							<el-select v-model="form.Open" placeholder="无条件共享">
								<el-option label="有条件共享" value="1"></el-option>
								<el-option label="无条件共享" value="0"></el-option>
							</el-select>
						</el-form-item>
						<el-form-item label="密钥认证：">
							<el-input type="textarea" :rows="3" v-model="form.SecretKey"></el-input>
						</el-form-item>
					</div>
					
				</el-form>
				<el-button class="btnclass1" @click="onquxiao">取消</el-button>
				<el-button class="btnclass2" @click="onUpadate">修改</el-button>
			</div>
		</el-dialog>
	
	</div>
</template>

<style>

</style>

<style lang="less" scoped>
@import "./List.less";
</style>

<script>

import {mapState} from 'vuex'
import api from "../../api/qingpu-api";

export default {
	name: "List",
		data() {
		return {
				tableDataList:[],
				pageSize: 8,
				currentPage:1,
				dialogtitle:'选择绑定的目录',
				dialogVisible:false,
				cataList:[],
				pageSize2: 6,
				currentPage2:1,
                cid:'',
				cpath:'',
				dialogtitleLook:'查看详情',
				dialogVisibleLook:false,
				showLook:false,
				showUpt:false,
				form:{
					rcxinxi:'',
					apiaddress:'',
					apimiaoshu:'',
					methods:'',
					id:'',
					condition:'0',
					name:'',
					time:'',
					effective:'',
					desc:'',
					Example:'',
					ApiName:'',
					Agreement:'',
					rType:'',
					ServiceName:'',
					ServiceType:'',
					ServiceHouse:'',
					PublishingTime:'',
					Open:'0',
					SecretKey:''
				},
				uid:'',
				saveIsMount:'',
				pickerOptionsEnd: {
					disabledDate: (time) => {
						var date1 = new Date(this.form.time)
						var date2 = new Date(date1)
						return time.getTime() < date2.getTime() 
					}
				}
		};
	},
	computed:{
		...mapState(['userId'])
	},
	methods: {
	    current_change:function(currentPage){
			this.currentPage = currentPage;
		},
		current_change2:function(currentPage2){
			this.currentPage2 = currentPage2;
		},
		handleEdit(b){
            this.dialogVisible = true
			var self = this
			self.cpath = b.address
			var self = this
			var param = {
				user:this.$store.state.userId
			}
			api.cataList(param).then(res => {
				//console.log(res.data)
				if (res.code == 200) {
					self.cataList = []
				    res.data.map((value,index)=>{
						//console.log(value)
					    var data = { index:"",bsname:'',id:"",name:'',type:'',status:""}
						   data.index = index +1
						   data.bsname = value.cata_code
						   data.id = value.cata_id
						   data.name = value.cata_title
						   data.type = value.org_name
						   data.status = value.cata_status_cn
						   self.cataList.push(data)
					})
				}

            })
		},
		handleClose() {
			this.dialogVisible = false
		},
		Edittype(b) {
			var self = this
			var param = {
				cata_id:b.id,
				userid:this.$store.state.userId,
				api_path:self.cpath
			}
            api.BindListApi(param).then(res => {
				if (res.code == 200) {
                    if (res.code == 200) {
						self.$message({
							message:"绑定成功",
							type:'success'
						})
						self.dialogVisible = false
						// var c = { id:self.cid } 
						// self.handleEdit(c)
						self.init()
					}
				}
			})
		},
		init() {
			var self = this
			//console.log(this.$store.state)
			var param = {
				userid:this.$store.state.userId
			}
			//console.log(123+	self.$store.state.userId)
			
			api.TotalList(param).then(res => {
				if(res.code == 200) {
					self.tableDataList = []
				    res.data.list.map((value,index)=>{
					    var data = { index:"",name:'',address:'',miaoshu:'',status:"",id:'',CHname:''}
						if(value.is_mount == 0){
                            data.status = "未绑定"    //目录绑定状态
						}
						else{
							data.status = "已绑定"    //目录绑定状态
						}
						   data.index = index +1
						   data.name = value.api_name
						   data.id = value.id
						   data.CHname = value.cataId
						   data.address = value.api_path
						   data.miaoshu = value.api_desc       //api描述
						   self.tableDataList.push(data)
					})
				}
            })
		},
		Looktype(b) {
            this.dialogVisibleLook = true
			this.showLook = true
			this.showUpt = false
            var param = {
				id:b.id
			}
			api.LookApi(param).then(res => {
				if(res.code == 200) {
				
                    var time = new Date(parseInt(res.data[0].registerDate) * 1000);
					let year = time.getFullYear()
					let month = time.getMonth() + 1
					let date = time.getDate()
					let hours = time.getHours()
					let minute = time.getMinutes()
					let second = time.getSeconds()

					if (month < 10) { month = '0' + month }
					if (date < 10) { date = '0' + date }
					if (hours < 10) { hours = '0' + hours }
					if (minute < 10) { minute = '0' + minute }
					if (second < 10) { second = '0' + second }
					let  start_timer= year + '-' + month + '-' + date + ' ' + hours + ':' + minute + ':' + second
					this.form.time = start_timer


					var time2 = new Date(parseInt(res.data[0].validityDate) * 1000);
					let year2 = time2.getFullYear()
					let month2 = time2.getMonth() + 1
					let date2 = time2.getDate()
					let hours2 = time2.getHours()
					let minute2 = time2.getMinutes()
					let second2 = time2.getSeconds()

					if (month2 < 10) { month2 = '0' + month2 }
					if (date2 < 10) { date2 = '0' + date2 }
					if (hours2 < 10) { hours2 = '0' + hours2}
					if (minute2 < 10) { minute2= '0' + minute2 }
					if (second2< 10) { second2 = '0' + second2 }
					let  end_timer= year2 + '-' + month2+ '-' + date2 + ' ' + hours2 + ':' + minute2 + ':' + second2
					this.form.effective = end_timer

					var time3 = new Date(parseInt(res.data[0].publishDate) * 1000);
					let year3 = time3.getFullYear()
					let month3 = time3.getMonth() + 1
					let date3 = time3.getDate()
					let hours3 = time3.getHours()
					let minute3 = time3.getMinutes()
					let second3 = time3.getSeconds()

					if (month3 < 10) { month3 = '0' + month3 }
					if (date3 < 10) { date3 = '0' + date3}
					if (hours3 < 10) { hours3 = '0' + hours3 }
					if (minute3 < 10) { minute3 = '0' + minute3 }
					if (second3 < 10) { second3 = '0' + second3 }
					let  out_timer= year3 + '-' + month3 + '-' + date3+ ' ' + hours3 + ':' + minute3 + ':' + second3
					this.form.PublishingTime = out_timer


					this.form.rcxinxi =res.data[0].param_info
					this.form.Example =res.data[0].case_info
					this.form.apiaddress =res.data[0].api_path
					this.form.ApiName =res.data[0].api_name
					this.form.apimiaoshu =res.data[0].api_desc
					this.form.Agreement =res.data[0].agreement
					this.form.methods =res.data[0].request_mode
					this.form.rType =res.data[0].back_type
					this.form.desc =res.data[0].serviceDesc
					this.form.ServiceType =res.data[0].version
					this.form.name=res.data[0].register
					this.$store.state.userId =res.data[0].registerId
					this.form.ServiceName =res.data[0].chnName
					// this.form.time =res.data[0].registerDate
					//this.form.PublishingTime =res.data[0].publishDate
					// this.form.effective =res.data[0].validityDate
					this.form.Open =res.data[0].isPublic
					this.form.condition =res.data[0].requireFile
					this.form.SecretKey =res.data[0].secret_key
					this.form.id =res.data[0].cataId
				}
            })
		},
		handleCloseLook() {
			this.dialogVisibleLook = false
			this.showLook = false
			this.showUpt = false
		},
		Updateype(b) {
			this.uid = b.id
			this.dialogVisibleLook = true
			this.showLook = false
			this.showUpt = true
			this.form = {
				rcxinxi:'',
				apiaddress:'',
				apimiaoshu:'',
				methods:'',
				id:'',
				condition:'',
				name:'',
				time:'',
				effective:'',
				desc:'',
				Example:'',
				ApiName:'',
				Agreement:'',
				rType:'',
				ServiceName:'',
				ServiceType:'',
				ServiceHouse:'',
				PublishingTime:'',
				Open:'',
				SecretKey:''
			}
			this.dialogtitleLook = "详情修改"
			var param = {
				id:b.id
			}
			api.LookApi(param).then(res => {
				if(res.code == 200) {
					var time = new Date(parseInt(res.data[0].registerDate) * 1000);
					let year = time.getFullYear()
					let month = time.getMonth() + 1
					let date = time.getDate()
					let hours = time.getHours()
					let minute = time.getMinutes()
					let second = time.getSeconds()

					if (month < 10) { month = '0' + month }
					if (date < 10) { date = '0' + date }
					if (hours < 10) { hours = '0' + hours }
					if (minute < 10) { minute = '0' + minute }
					if (second < 10) { second = '0' + second }
					let  start_timer= year + '-' + month + '-' + date + ' ' + hours + ':' + minute + ':' + second
					this.form.time = start_timer


					var time2 = new Date(parseInt(res.data[0].validityDate) * 1000);
					let year2 = time2.getFullYear()
					let month2 = time2.getMonth() + 1
					let date2 = time2.getDate()
					let hours2 = time2.getHours()
					let minute2 = time2.getMinutes()
					let second2 = time2.getSeconds()

					if (month2 < 10) { month2 = '0' + month2 }
					if (date2 < 10) { date2 = '0' + date2 }
					if (hours2 < 10) { hours2 = '0' + hours2}
					if (minute2 < 10) { minute2= '0' + minute2 }
					if (second2< 10) { second2 = '0' + second2 }
					let  end_timer= year2 + '-' + month2+ '-' + date2 + ' ' + hours2 + ':' + minute2 + ':' + second2
					this.form.effective = end_timer

					var time3 = new Date(parseInt(res.data[0].publishDate) * 1000);
					let year3 = time3.getFullYear()
					let month3 = time3.getMonth() + 1
					let date3 = time3.getDate()
					let hours3 = time3.getHours()
					let minute3 = time3.getMinutes()
					let second3 = time3.getSeconds()

					if (month3 < 10) { month3 = '0' + month3 }
					if (date3 < 10) { date3 = '0' + date3}
					if (hours3 < 10) { hours3 = '0' + hours3 }
					if (minute3 < 10) { minute3 = '0' + minute3 }
					if (second3 < 10) { second3 = '0' + second3 }
					let  out_timer= year3 + '-' + month3 + '-' + date3+ ' ' + hours3 + ':' + minute3 + ':' + second3
					this.form.PublishingTime = out_timer
                    //console.log(out_timer)
					this.form.rcxinxi =res.data[0].param_info
					this.form.Example =res.data[0].case_info
					this.form.apiaddress =res.data[0].api_path
					this.form.ApiName =res.data[0].api_name
					this.form.apimiaoshu =res.data[0].api_desc
					this.form.Agreement =res.data[0].agreement
					this.form.methods =res.data[0].request_mode
					this.form.rType =res.data[0].back_type
					this.form.desc =res.data[0].serviceDesc
					this.form.ServiceType =res.data[0].version
					this.form.name=res.data[0].register
					this.$store.state.userId =res.data[0].registerId
					this.form.ServiceName =res.data[0].chnName
					// this.form.time =res.data[0].registerDate
					// this.form.PublishingTime =res.data[0].publishDate
					// this.form.effective =res.data[0].validityDate
					this.form.Open =res.data[0].isPublic
					this.form.condition =res.data[0].requireFile
					this.form.SecretKey =res.data[0].secret_key
					this.saveIsMount = res.data[0].is_mount
				}
            })
		},
		onquxiao() {
            this.form = {
				rcxinxi:'',
				apiaddress:'',
				apimiaoshu:'',
				methods:'',
				id:'',
				condition:'',
				name:'',
				time:'',
				effective:'',
				desc:'',
				Example:'',
				ApiName:'',
				Agreement:'',
				rType:'',
				ServiceName:'',
				ServiceType:'',
				ServiceHouse:'',
				PublishingTime:'',
				Open:'',
				SecretKey:''
			}

		},
		onUpadate() {
			var self = this
			var time1 = this.form.time
			var time2 = this.form.effective
			var time3 = this.form.PublishingTime
			var timestamp1 = Date.parse(new Date(time1));
			timestamp1 = timestamp1 / 1000;
			var timestamp2 = Date.parse(new Date(time2));
			timestamp2 = timestamp2 / 1000;
			var timestamp3 = Date.parse(new Date(time3));
			timestamp3 = timestamp3 / 1000;

			//console.log(timestamp3)
			var param = {
				param_info: this.form.rcxinxi,
				case_info: this.form.Example,
				api_path: this.form.apiaddress,
				api_name: this.form.ApiName,
				api_desc: this.form.apimiaoshu,
				agreement: this.form.Agreement,
				request_mode: this.form.methods,
				back_type: this.form.rType,
				serviceDesc: this.form.desc,
				version: this.form.ServiceType,
				register: this.form.name,
				registerId: this.$store.state.userId,
				chnName: this.form.ServiceName,
				registerDate:timestamp1,
				publishDate: timestamp3,
				validityDate: timestamp2,
				isPublic: this.form.Open,
				requireFile: this.form.condition,
				secret_key: this.form.SecretKey,
				id:this.uid,
				is_mount:this.saveIsMount
			}
			api.UpdateApi(param).then(res => {
				if(res.code == 200) {
					self.$message({
						message:"修改成功",
						type:'success'
					})
					self.dialogVisibleLook = false
					self.init()
				}
            })
		},
		Deltype(b) {
			//console.log(b)
			//DelApi
			var self = this
			var param = {
				id:b.id,
				is_mount:b.status
			}
			api.DelApi(param).then(res => {
				if(res.code == 200) {
					self.$message({
						message:"删除成功",
						type:'success'
					})
					self.init()
				}
            })
		}

	},
	mounted() {
		this.init()
	}
};
</script>





