
<template>
	<div class="login">
        <div class="loginBox">
		    <div class="title">API登录系统</div>
			<div class="item">
				<div class="itemname">用户名</div>
				<div class="iteminput">
					<input type="text" placeholder="请输入用户名id" v-model="userName">
				</div>
			</div>
			<div class="item" id="active">
				<div class="itemname">密码</div>
				<div class="iteminput">
					<input type="password" placeholder="请输入密码" v-model="password">
				</div>
			</div>
			<div class="submit" @click="submit">登 录</div>

		</div>
	</div>
</template>

<style>

</style>

<style lang="less" scoped>
@import "./login.less";
</style>

<script>

import { mapMutations,mapState } from 'vuex'
import api from "../../api/qingpu-api";
export default {
	name: "Login",
	data() {
		return {
			userName: "",
			password: ""
		};
	},
  computed:{

  },
  mounted() {
	  this.init()
  },
	methods: {
    ...mapMutations(['getUser']),
		submit(){
		    var self =this
			let data = {
				userid: this.userName,
				password: this.password,
			};
			api.logining(data).then(res => {
				if (res.code == 200) {
					localStorage.setItem('userid',self.userName);
					self.getUser(this.userName);
					if(this.userName == "admin") {
                        self.$router.push({
							path: '/user'
						})
					}
					else{
						self.$router.push({
							path: '/home'
						})
						localStorage.setItem('accessToken',"componentRegister")
					}
					
				}
            })
		},
		init() {
			if( localStorage.getItem("userid") && localStorage.getItem("userid") != "admin") {
				this.$router.push({
					path: '/home'
				})
			}
			else if(localStorage.getItem("userid") == "admin"){
				this.$router.push({
					path: '/user'
				})
			}
			else{

			}
		}

	}
};
</script>





