<template>
	<div class="register">
        <el-form ref="form" :model="form" label-width="38%" :rules="rules">
            <div class="left">
                <el-form-item label="入参信息："  prop="rcxinxi">
                    <el-input v-model="form.rcxinxi"></el-input>
                </el-form-item>
                <el-form-item label="API地址："  prop="apiaddress">
                    <el-input v-model="form.apiaddress"></el-input>
                </el-form-item>
                <el-form-item label="API描述：" prop="apimiaoshu">
                    <el-input v-model="form.apimiaoshu"></el-input>
                </el-form-item>
                <el-form-item label="请求方式：" prop="methods">
                    <el-input v-model="form.methods"></el-input>
                </el-form-item>
              
                <el-form-item label="申请服务是否必须携带附件：">
                    <el-select v-model="form.condition" >
                        <el-option label="是" value="1"></el-option>
                        <el-option label="否" value="0"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="服务注册者姓名：" prop="name">
                    <el-input v-model="form.name"></el-input>
                </el-form-item>
                <!--<el-form-item label="注册时间：">
                    <el-input v-model="form.time"></el-input>
                </el-form-item>
                <el-form-item label="服务有效日期：">
                    <el-input v-model="form.effective"></el-input>
                </el-form-item>
                  <el-form-item label="目录资源ID：" prop="id">
                    <el-input v-model="form.id" disabled></el-input>
                </el-form-item>
                 -->

                <el-form-item label="注册时间：" prop="time">
                   	 <el-date-picker
                            v-model="form.time"
                            type="datetime"
                            format="yyyy-MM-dd hh:mm"
                            value-format="yyyy-MM-dd hh:mm"
                            placeholder="选择注册时间">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="截止有效日期：" prop="effective">
                     	 <el-date-picker
                            v-model="form.effective"
                            type="datetime"
                            format="yyyy-MM-dd hh:mm"
                            value-format="yyyy-MM-dd hh:mm"
                            :picker-options="pickerOptionsEnd"
                            placeholder="选择截止有效日期">
                    </el-date-picker>
                </el-form-item> 

                <el-form-item label="服务描述：" prop="desc">
                    <el-input type="textarea" :rows="3" v-model="form.desc"></el-input>
                </el-form-item>

            </div>
             <div class="right">
                <el-form-item label="实例信息：" prop="Example">
                    <el-input v-model="form.Example"></el-input>
                </el-form-item>
                <el-form-item label="API名称：" prop="ApiName">
                    <el-input v-model="form.ApiName"></el-input>
                </el-form-item>
                <el-form-item label="协议：" prop="Agreement">
                    <el-input v-model="form.Agreement"></el-input>
                </el-form-item>
                <el-form-item label="返回类型：" prop="rType">
                    <el-input v-model="form.rType"></el-input>
                </el-form-item>
                <el-form-item label="服务中文名称：" prop="ServiceName">
                    <el-input v-model="form.ServiceName"></el-input>
                </el-form-item>
                  <el-form-item label="服务版本：" prop="ServiceType">
                    <el-input v-model="form.ServiceType"></el-input>
                </el-form-item>
                <!--
                  <el-form-item label="服务注册机构名称：" prop="ServiceHouse">
                    <el-input v-model="form.ServiceHouse" disabled></el-input>
                </el-form-item>
                -->
                 <el-form-item label="发布时间：" prop="PublishingTime">
                    <el-date-picker
                            v-model="form.PublishingTime"
                            type="datetime"
                            format="yyyy-MM-dd hh:mm"
                            value-format="yyyy-MM-dd hh:mm"
                            placeholder="选择发布时间">
                    </el-date-picker>
                </el-form-item>

                 <el-form-item label="公开方式：" >
                    <el-select v-model="form.Open" >
                        <el-option label="有条件共享" value="1"></el-option>
                        <el-option label="无条件共享" value="0"></el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="密钥认证：" prop="SecretKey">
                    <el-input type="textarea" :rows="3" v-model="form.SecretKey"></el-input>
                </el-form-item>

            </div>
            <el-button class="btnclass" @click="onSubmit('form')">注册</el-button>
        </el-form>
	</div>
</template>

<style>

</style>

<style lang="less" scoped>
@import "./register.less";
</style>

<script>

  import {mapState} from 'vuex'
import api from "../../api/qingpu-api";
export default {
	name: "register",
	data() {
		return {
            form:{
                rcxinxi:'',
                apiaddress:'',
                apimiaoshu:'',
                methods:'',
                id:'',
                condition:'0',
                name:'',
                time:'',
                effective:'',
                desc:'',
                Example:'',
                ApiName:'',
                Agreement:'',
                rType:'',
                ServiceName:'',
                ServiceType:'',
                ServiceHouse:'',
                PublishingTime:'',
                Open:'0',
                SecretKey:''
            },
            rules:{
                rcxinxi: [
                    { required: true, message: '不能为空', trigger: 'blur' },
                ],
                apiaddress: [
                    { required: true, message: '不能为空', trigger: 'blur' },
                ],
                apimiaoshu: [
                    { required: true, message: '不能为空', trigger: 'blur' },
                ],
                methods: [
                    { required: true, message: '不能为空', trigger: 'blur' },
                ],
                id: [
                    { required: true, message: '不能为空', trigger: 'blur' },
                ],
                name: [
                    { required: true, message: '不能为空', trigger: 'blur' },
                ],
                time: [
                    { required: true, message: '不能为空', trigger: 'blur' },
                ],
                effective: [
                    { required: true, message: '不能为空', trigger: 'blur' },
                ],
                desc: [
                    { required: true, message: '不能为空', trigger: 'blur' },
                ],
                Example: [
                    { required: true, message: '不能为空', trigger: 'blur' },
                ],
                ApiName: [
                    { required: true, message: '不能为空', trigger: 'blur' },
                ],
                Agreement: [
                    { required: true, message: '不能为空', trigger: 'blur' },
                ],
                rType: [
                    { required: true, message: '不能为空', trigger: 'blur' },
                ],
                ServiceName:[
                    { required: true, message: '不能为空', trigger: 'blur' },
                ],
                ServiceType: [
                    { required: true, message: '不能为空', trigger: 'blur' },
                ],
                ServiceHouse: [
                    { required: true, message: '不能为空', trigger: 'blur' },
                ],
                PublishingTime: [
                    { required: true, message: '不能为空', trigger: 'blur' },
                ],
                SecretKey: [
                    { required: true, message: '不能为空', trigger: 'blur' },
                ],
            },
            pickerOptionsEnd: {
                disabledDate: (time) => {
                    var date1 = new Date(this.form.time)
                    var date2 = new Date(date1)
                    return time.getTime() < date2.getTime() 
                }
            }
     
		};
	},
  computed:{
    ...mapState(['userId'])
  },
  mounted() {
	  this.init()
  },
   
// watch: {
//     'form.time': function() {
//         console.log(132)
//     },
//     deep: true
// },
  methods: {
        init() {
        //  console.log(this.$store.state.userId)
        },

        onSubmit(form) {
            var self = this
            this.$refs[form].validate((valid) => {
                if (valid) {
                    
                    var time1 = this.form.time
                    var time2 = this.form.effective
                    var time3 = this.form.PublishingTime
                    var timestamp1 = Date.parse(new Date(time1));
                    timestamp1 = timestamp1 / 1000;
                    var timestamp2 = Date.parse(new Date(time2));
                    timestamp2 = timestamp2 / 1000;
                    var timestamp3 = Date.parse(new Date(time3));
                    timestamp3 = timestamp3 / 1000;
                    var param = {
                        param_info: this.form.rcxinxi,
                        case_info: this.form.Example,
                        api_path: this.form.apiaddress,
                        api_name: this.form.ApiName,
                        api_desc: this.form.apimiaoshu,
                        agreement: this.form.Agreement,
                        request_mode: this.form.methods,
                        back_type: this.form.rType,
                        serviceDesc: this.form.desc,
                        version: this.form.ServiceType,
                        register: this.form.name,
                        registerId: this.$store.state.userId,
                        chnName: this.form.ServiceName,
                        registerDate: timestamp1,
                        publishDate: timestamp3,
                        validityDate: timestamp2,
                        isPublic: this.form.Open,
                        requireFile: this.form.condition,
                        secret_key: this.form.SecretKey
                    }
                    //console.log(param)
                    api.register(param).then(res => {
                        console.log(res.data)
                        if (res.code == 200) {
                            self.$message({
                                message:"注册成功",
                                type:'success'
                            })
                            self.form  = {
                                rcxinxi:'',
                                apiaddress:'',
                                apimiaoshu:'',
                                methods:'',
                                id:'',
                                condition:'0',
                                name:'',
                                time:'',
                                effective:'',
                                desc:'',
                                Example:'',
                                ApiName:'',
                                Agreement:'',
                                rType:'',
                                ServiceName:'',
                                ServiceType:'',
                                ServiceHouse:'',
                                PublishingTime:'',
                                Open:'0',
                                SecretKey:''
                            }
                        }
                        else if (res.code == 300) {
                            self.$message({
                                message:res.message+",且API地址不能重复",
                                type:'error'
                            })
                        }

                    })
                } else {
                    console.log('error submit!!');
                    return false;
                }
                });
         
        },

    },
 
};
</script>





