<template>
	<div class="header">
	    <!-- <div class="logoname"><img :src="require('./images/logoname.png')"></div> 
        <div class="userbox">
			<div class="tongzhi">
				<div class="tzimg"><img :src="require('./images/tongzhi.png')"></div>
				<div class="tznum">{{message}}</div>
		    </div>
			<div class="touxiang"><img :src="require('./images/user.png')"></div>
			<div class="username">
				<el-dropdown @command="handleCommand">
					<span class="el-dropdown-link">
						{{username}}<i class="el-icon-arrow-down el-icon--right"></i>
					</span>
						<el-dropdown-menu slot="dropdown">
							<el-dropdown-item command="b" divided >退出</el-dropdown-item>
						</el-dropdown-menu>
					</el-dropdown>
			</div>
		</div> -->
		<div class="logoname"></div> 
        <div class="userbox">
			<div class="tongzhi">
				
		    </div>
			<div class="username">
				<el-dropdown @command="handleCommand">
					<span class="el-dropdown-link">
						<div>{{username}}</div><i class="el-icon-arrow-down el-icon--right"></i>
					</span>
						<el-dropdown-menu slot="dropdown">
							<el-dropdown-item command="b"  >退出</el-dropdown-item>
						</el-dropdown-menu>
					</el-dropdown>
			</div>
		</div>
	</div>
</template>

<style>

</style>

<style lang="less" scoped>
@import "./headers.less";
</style>

<script>

import {mapMutations, mapState} from 'vuex'
import api from "../../api/qingpu-api";

export default {
	name: "headers",
	data() {
		return {
			message:2,
			username:''
		};
	},
  computed:{
    ...mapState(['userId'])
  },
  mounted() {
	    this.username = localStorage.getItem('userid');
  },
	methods: {
		...mapMutations(['getUser']),
	    handleCommand() {
			var self = this
			api.logouting().then(res => {
				if(res.code == 200) {
					self.$message({
						message:"退出成功",
						type:'success'
					})
					localStorage.removeItem('userid');
					localStorage.clear();
					self.$router.push({
						path: '/'
					})
				}
				
			})

		},

	}
};
</script>





