import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
  userId: localStorage.getItem("userid"),
  pageType:'componentRegister'
}
const getters = {

}
const mutations = {
    getUser(state,data){
        localStorage.setItem("userid", data);
        state.userId = data;
    },
    hangdlePageType(state,data) {
        state.pageType = data
    }

}
const actions = {
    setName(context,str){
        context.commit("userId", str);
    }
}

const store = new Vuex.Store({
    state,
    getters,
    mutations,
    actions,
})

export default store



