<template>
	<div class="Resources">
       <el-table :data="tableDataList.slice((currentPage - 1) * pageSize, currentPage * pageSize)">
            <el-table-column prop="index" label="序号">
	        </el-table-column>
			<el-table-column prop="bsname" label="目录标识号">
            </el-table-column>
	    	<el-table-column prop="id" label="目录ID">
	        </el-table-column>
	        <el-table-column prop="name" label="目录名称">
	        </el-table-column>
	        <el-table-column prop="type" label="所属单位">
	        </el-table-column>
	        <el-table-column prop="status" label="目录状态">
	        </el-table-column>
			<el-table-column label="资源操作">
	        	<template slot-scope="scope">
						<span
						    class="fontSize"
							@click="handleEdit(scope.row)">绑定API
						</span>
					</template>
	        </el-table-column>
	    </el-table>
		<el-pagination 
			background
			layout="prev, pager, next"
			:current-page="currentPage"
			:page-size="pageSize"
			@current-change="current_change"
			:total="tableDataList.length">
		</el-pagination>
		<el-dialog
			:title="dialogtitle"
			:visible.sync="dialogVisible"
			:modal-append-to-body='false'
			width="80%"
			:before-close="handleClose">
			<div class="maskTable">
				<el-table :data="APIList.slice((currentPage2 - 1) * pageSize2,currentPage2*pageSize2)">
					<el-table-column prop="ApiIndex" label="序号">
					</el-table-column>
					<el-table-column prop="ApiName" label="API名称">
					</el-table-column>
					<el-table-column prop="ApiAddress" label="API地址">
					</el-table-column>
					<el-table-column prop="ApiDescribe" label="API描述">
					</el-table-column>
				
					<el-table-column label="操作">
						<template slot-scope="scope">
						<span
						    class="fontSize"
							@click="handleAPI( scope.row)">点击确定
						</span>
					</template>
					</el-table-column>
				</el-table>
				<el-pagination 
					background
					layout="prev, pager, next"
					:current-page="currentPage2"
					:page-size="pageSize2"
					@current-change="current_change2"
					:total="APIList.length">
				</el-pagination>
			</div>
		</el-dialog>
		
	</div>
</template>

<style>

</style>

<style lang="less" scoped>

@import "./Resources.less";

</style>

<script>

import {mapState} from 'vuex'
import api from "../../api/qingpu-api";

export default {
	name: "Resources",
	data() {
		return {
				tableDataList: [
		              {
						  index:1,
						  bsname:"aa",
						  name:"bb",
						  id:2,
						  type:'213',
						  status:"正常",

					  },
					     {
						  index:1,
						  bsname:"aa",
						  name:"bb",
						  id:2,
						  type:'213',
						  status:"正常",

					  },
					     {
						  index:1,
						  bsname:"aa",
						  name:"bb",
						  id:2,
						  type:'213',
						  status:"正常",

					  },
					     {
						  index:1,
						  bsname:"aa",
						  name:"bb",
						  id:2,
						  type:'213',
						  status:"正常",

					  },
					     {
						  index:1,
						  bsname:"aa",
						  name:"bb",
						  id:2,
						  type:'213',
						  status:"正常",

					  },   {
						  index:1,
						  bsname:"aa",
						  name:"bb",
						  id:2,
						  type:'213',
						  status:"正常",

					  }
					  ,   {
						  index:1,
						  bsname:"aa",
						  name:"bb",
						  id:2,
						  type:'213',
						  status:"正常",

					  }
				],
				pageSize: 8,
				currentPage:1,
				APIList:[],
				pageSize2: 6,
				currentPage2:1,
				Cid:"",
				dialogtitle:'选择绑定的API',
				dialogVisible:false,
			};
	},
	computed:{
		...mapState(['userId'])
	},
	methods: {
	    current_change:function(currentPage){
			this.currentPage = currentPage;
		},
		current_change2:function(currentPage2){
			this.currentPage2 = currentPage2;
		},
		handleEdit(b){
			//console.log(a,b)
			var self = this
			this.dialogVisible = true
			self.Cid = b.id
			var param = {
				//Cata_id:b.id,
				userid:this.$store.state.userId
			}
            api.Bindcata(param).then(res => {
				//console.log(res.data.list)
				if (res.code == 200) {
                    self.APIList = []
				    res.data.list.map((value,index)=>{
						//console.log(value)
					    var data = { ApiIndex:"",ApiName:'',ApiAddress:"",ApiDescribe:''}
							data.ApiIndex = index +1     
							data.ApiName = value.api_name 
							data.ApiAddress = value.api_path  
							data.ApiDescribe = value.api_desc  
							//data.ApiStatus = value.is_mount  
							self.APIList.push(data)
			        })
				}
			})
		},
		handleAPI(b) {
            //console.log(a,b)
			var self = this
			//console.log(b)
			var param = {
				cata_id:self.Cid,
				userid:this.$store.state.userId,
				api_path:b.ApiAddress
			}
            api.BindCataApi(param).then(res => {
				if (res.code == 200) {
                    if (res.code == 200) {
						self.$message({
							message:"绑定成功",
							type:'success'
						})
						var c = { id:self.Cid } 
						self.handleEdit(c)
						// self.dialogVisible = false
						// self.init()
					}
				}
			})
		},
	    handleClose() {
			this.dialogVisible = false
		},
		init() {
			var self = this
			var param = {
				userid:this.$store.state.userId
			}
			api.cataList(param).then(res => {
				//console.log(res.data)
				if (res.code == 200) {
					self.tableDataList = []
				    res.data.map((value,index)=>{
						//console.log(value)
					    var data = { index:"",bsname:'',id:"",name:'',type:'',status:""}
						   data.index = index +1
						   data.bsname = value.cata_code
						   data.id = value.cata_id
						   data.name = value.cata_title
						   data.type = value.org_name
						   data.status = value.cata_status_cn
						   self.tableDataList.push(data)
					})
				}

            })
			
		}
 
	},
	mounted() {
		this.init()
	}
};
</script>





