<template>
	<div class="user">
	    <div class="headers">
			<componentHeader></componentHeader>
		</div>
	    <div class="left">

		</div>
		<div class="nav1 active"
			@click="switchType(1)"
			:class="{'btnClass': show== true}">
			<div class="NavImg">
				<span  v-if="show"> <img :src="require('./images/zonglan.png')"/></span>
				<span v-else-if="!show"><img :src="require('./images/zonglian2.png')" /></span>
			</div>
			<div class="NavName"  :class="{'wordClass': show== true}">总览</div>
		</div>
		<div class="nav2"
			@click="switchType(2)"
			:class="{'btnClass': !show== true}">
			<div class="NavImg">
				<span  v-if="show"> <img :src="require('./images/rizhi.png')"/></span>
				<span v-else-if="!show"><img :src="require('./images/rizhitype.png')" /></span>
			</div>
			<div :class="{'wordClass': !show == true}" class="NavName">日志详情</div>
		</div>
		<div class="right">
			<div class="rtotal">
				<div class="Alltotal1" id="Aactive">
					<div class="Atitle">接口当日调用次数</div>
					<div class="Anum">{{todayNum}}</div>
				<!--	<div class="Anumber">
						<div class="Animg"><img :src="require('./images/top.png')"></div>
						<div class="Annun">+22.44%</div>
						<div class="Anname">与上月同比</div>
					</div> -->
				</div>
				<div class="Alltotal2">
					<div class="Atitle">接口当月调用次数</div>
					<div class="Anum">{{munthNum}}</div>
				
				</div>
				<div class="Alltotal3">
					<div class="Atitle">接口调用总次数</div>
					<div class="Anum">{{allNum}}</div>
				
				</div>
			</div>
			<div class="rtitle">接口调用量排行情况</div>
			<div class="rtable">
				  <el-table :data="tableDataList">
						<el-table-column prop="ApiRanking" label="排行">
						</el-table-column>
						<el-table-column prop="ApiName" label="名称" width="640%">
						</el-table-column>
						<el-table-column prop="ApiTotal" label="调用量">
						</el-table-column>
						<el-table-column label="操作">
							<template slot-scope="scope">
								<img :src="scope.row.image" alt="图片" width="26"/>
							</template>
						</el-table-column>
					</el-table>
			</div>
			<el-pagination 
				layout="prev, pager, next"
				:current-page="currentPage"
				:page-size="pageSize"
				@current-change="current_change"
				:total="tableDataList.length">
			</el-pagination>
		</div>

	</div>
</template>
<style lang="less" scoped>
@import "./user.less";

</style>

<script>
import { mapState,mapMutations } from 'vuex'
import api from "../../api/qingpu-api";

import componentHeader from '../../components/headers/headers'
import img1 from './images/tool.png'

export default {
	name: "user",
	data() {
		return{
			show:true,
		    tableDataList:[	
			],
			todayNum:'',
			munthNum:'',
			allNum:'',
			pageSize: 6,
			currentPage:1,
		}
	},
	components:{
        componentHeader
	},
	mounted(){
		this.init()
	},
	created(){


	},
	methods:{
         current_change:function(currentPage){
			this.currentPage = currentPage;
		}, 
		init(){
			var self = this
			if( localStorage.getItem("userid") != "admin") {
				this.$router.push({
					path: '/home'
				})
			}

            api.Allnum().then(res=>{
                if(res.code == 200){
					//console.log(res.data)
					self.tableDataList = []
					self.munthNum = res.data.month_call_number
					self.todayNum = res.data.today_call_number
					self.allNum = res.data.all_call_number
					res.data.list.map((value,index)=>{
                        var data = {
							ApiRanking:'',
							ApiName:'',
							ApiTotal:'',
							image:''
						}
						data.ApiRanking = index+1
						data.ApiName = value.api
						data.ApiTotal = value.call_number
						data.image = img1
						self.tableDataList.push(data)
					})
				}
			})
		},
		switchType(ev) {
			if(ev == 1) {
				this.show = true
			}
			else{
				this.show = false
			}
		}
	}
}
</script>





